<template lang="pug">
  b-form-textarea#cus-textarea(
    v-model.trim='valueInner'
    ref="autoResizeTextarea"
    placeholder='請輸入...'
    @compositionstart="isTyping = true"
    @compositionend="isTyping = false"
    @keydown.enter.exact.prevent="handleKeyEnter"
    @update="autoResize"
  )
</template>

<script>
export default {
  props: {
    value: {
      type: String,
      default: () => null
    }
  },
  data() {
    return {
      isTyping: false,
    }
  },
  computed: {
    valueInner: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('input', value);
      }
    }
  },
  methods: {
    setEmptyHeight() {
      this.$refs.autoResizeTextarea.$el.style.maxHeight = '38px';
    },
    autoResize() {
      const DOM = this.$refs.autoResizeTextarea.$el;
      let DOMScrHeight = DOM.scrollHeight;
      DOM.style.height = 0;
      DOM.style.height = DOM.scrollHeight < 40 ? '38px' : `${DOMScrHeight}px`;

      if(DOMScrHeight > 156) {
        DOM.style.maxHeight = '156px';
      } else if(DOMScrHeight > 80) {
        DOM.style.maxHeight = `${DOMScrHeight - 24}px`;
      } else if(DOMScrHeight < 40) {
        DOM.style.maxHeight = '38px';
      } else {
        DOM.style.maxHeight = `${DOMScrHeight}px`;
      }
    },
    handleKeyEnter() {
      if(!this.isTyping && this.valueInner) {
        this.$emit('handleEnter', this.valueInner);
        this.setEmptyHeight();
        this.valueInner = '';
      }
    }
  }
}
</script>

<style lang="scss">
#cus-textarea {
  height: 38px;
  max-height: 156px;
  resize: none;
  overflow-y: auto;
  white-space: pre-line;
  &::-webkit-scrollbar {
    background: #eee;
    width: 8px;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #bbb;
    border-radius: 8px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background-color: #999;
  }
}
</style>